import React from 'react';
import Section from '../Section/Section';
import BackgroundImgModelS from '../../images/model-s.jpg';
import BackgroundImgModelY from '../../images/model-y.jpg';
import BackgroundImgModel3 from '../../images/model-3.jpg';
import BackgroundImgModelX from '../../images/model-x.jpg';
import BackgroundImgSolarPanel from '../../images/solar-panel.jpg';
import BackgroundImgSolarRoof from '../../images/solar-roof.jpg';
import BackgroundImgAccessories from '../../images/accessories.jpg';
import './Home.css';

export default function Home() {
    return (
        <div className="home-container">
            <Section
                title="Model S"
                description="Order Online for Touchless Delivery"
                backgroundImg={BackgroundImgModelS}
                leftBtnText="Custom Order"
                rightBtnText="Existing Inventory"
            />
            <Section
                title="Model Y"
                description="Order Online for Touchless Delivery"
                backgroundImg={BackgroundImgModelY}
                leftBtnText="Custom Order"
                rightBtnText="Existing Inventory"
            />
            <Section
                title="Model 3"
                description="Order Online for Touchless Delivery"
                backgroundImg={BackgroundImgModel3}
                leftBtnText="Custom Order"
                rightBtnText="Existing Inventory"
            />
            <Section
                title="Model X"
                description="Order Online for Touchless Delivery"
                backgroundImg={BackgroundImgModelX}
                leftBtnText="Custom Order"
                rightBtnText="Existing Inventory"
            />
            <Section
                title="Lowest Cost Solar Panels in America"
                description="Money-back guarantee"
                backgroundImg={BackgroundImgSolarPanel}
                leftBtnText="Order Now"
                rightBtnText="Learn More"
            />
            <Section
                title="Solar for New Roofs"
                description="Solar Roof Costs Less Than a New Roof Plus Solar Panels"
                backgroundImg={BackgroundImgSolarRoof}
                leftBtnText="Order Now"
                rightBtnText="Learn More"
            />
            <Section
                title="Accessories"
                description=""
                backgroundImg={BackgroundImgAccessories}
                leftBtnText="Shop Now"
            />
        </div>
    );
};