import React, { useState } from 'react';
import Logo from '../../images/logo.svg';
import { useSelector } from 'react-redux';
import { selectCars } from '../../fearures/Car/CarSlice';
import './Header.css';

export default function Header() {
    const [burgerStatus, setBurgerStatus] = useState(false);
    const cars = useSelector(selectCars);

    return (
        <div className="header-container">

            <a href="/home"><img src={Logo} alt="logo-img" /></a>

            <div className="center-menu">
                {cars && cars.map((car, index) => (
                    <a key={index} href={car}>{car}</a>
                ))}
                <a href="/solar-panels">Solar Panels</a>
                <a href="/accessories">Accessories</a>
            </div>

            <div className="rightMenu">
                <a href="/shop">Shop</a>
                <a href="/tesla-account">Tesla Account</a>
                <div onClick={() => setBurgerStatus(true)}>
                    <i className="burger-menu-icon fas fa-bars"></i>
                </div>
                <div
                    className="burger-nav"
                    style={{ transform: burgerStatus ? 'translateX(0)' : 'translateX(100%)' }}
                    onClick={() => setBurgerStatus(false)}>

                    <div className="close-wrapper">
                        <div className="custom-close" >
                            <i className="fas fa-times close-icon"></i>
                        </div>
                    </div>

                    {cars && cars.map((car, index) => (
                        <li key={index}><a href={car}>{car}</a></li>
                    ))}
                    <li><a href="/existing-inventory">Existing Inventory</a></li>
                    <li><a href="/used-inventory">Used Inventory</a></li>
                    <li><a href="/trade-in">Trade-in</a></li>
                    <li><a href="/cybertruck">Cybertruck</a></li>
                    <li><a href="/roadster">Roadster</a></li>
                </div>
            </div>
        </div >
    );
};